.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 30px;

    p {
        font-size: 20px;
        font-weight: bold;
    }

    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border-left: 5px solid #000 !important;
        border: 5px solid transparent;
        animation: spin 1s linear infinite;
    }

    @keyframes spin {
        to {
            transform: rotate(360deg);
        }
    }
}